@use "../../foundation";
.c-title {
  @include foundation.fz(40, 26);
  @include foundation.lh(1);
  letter-spacing: 0.03em;
  position: relative;

  // &::after {
  //   content: "";
  //   position: absolute;
  //   bottom: 0;
  //   right: 50%;
  //   left: 50%;
  //   transform: translate(-50%, 0);
  //   width: 80px;
  //   height: 8px;
  //   border-radius: 8px;
  //   background-color: foundation.$orange;
  // }

  &--h2 {
    position: relative;
    font-weight: bold;
    @include foundation.lh(1);
    letter-spacing: 0.03em;
    @include foundation.fz(36, 26);
    display: inline-block;
    padding: 10px 0;
    &::after {
      content: "";
      width: 0%;
      height: 3px;
      position: absolute;
      bottom: -10%;
      left: 0;
    }
  }
  &--h3 {
    position: relative;
    @include foundation.lh(1.5);
    letter-spacing: 0.03em;
    font-weight: 700;
    @include foundation.fz(21.25, 15);
    display: inline-block;
    text-align: center;
  }
  &--h4 {
    position: relative;
    @include foundation.lh(1);
  letter-spacing: 0.03em;
    @include foundation.fz(28, 20);
    @include foundation.tb{
      font-size: 2.25rem;
    }
    display: inline-block;
  }
}

@media screen and (max-width: 380px){
  .c-title--h3{
    font-size: 1.4rem;
  }
}

