@use '../../foundation';
.u-margin{

    &_top{
        &--SS{
            margin-top:10px;
        }
        &--S{
            margin-top:20px;
        }
        &--M{
            margin-top:40px;
            @include foundation.tb{
                margin-top:36px;
            }
            @include foundation.sp{
                margin-top:30px;
            }
        }
        &--L{
            margin-top:80px;
        @include foundation.tb{
            margin-top:60px ;
        }
        @include foundation.sp{
            margin-top:60px ;
        }
        }
    }

    &_left{
        &--S{
            margin-left:30px;
            @include foundation.sp{
                margin-left: 2px;
            }
        }
        &--SS{
            margin-left:10px;
            
        }
    }
    &_right{
        &--S{
            margin-right:30px;
            @include foundation.sp{
                margin-right: 2px;
            }
        }
        &--SS{
            margin-right:16px;
            @include foundation.sp{
                margin-right: 6px;
            }
        }
    }

    &_bottom{
        &--L{
            margin-bottom: 76px;
            @include foundation.sp{
                margin-bottom:56px;
            }
        }

        &--M{
            margin-bottom:40px;
            @include foundation.sp{
                margin-bottom: 30px;
            }
        }

        &--S{
            margin-bottom:30px;
            @include foundation.tb{
                margin-bottom: 20px;
            }
        }
    }
}