@use "../../foundation";

.p-top {
  padding: 30px 0 0;
  &_banner {
    padding: 10px 0;
    background-color: foundation.$p-blue;
    margin-top: 70px;

    @include foundation.sp {
      margin-top: 28px;
      padding: 6px 0;
    }

    & .la-container {
      @include foundation.tb {
        text-align: center;
      }
    }
  }

  &_cont {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    margin-left: 50px;
    @include foundation.tb {
      margin: 0;
    }
    &_cmn {
      &:nth-child(2) {
        width: calc(100% - 30%);
        padding: 40px 0;

        @include foundation.tb {
          width: 100%;
          padding: 20px 0 0 20px;
        }
        @include foundation.sp {
          padding: 20px 0 0 0px;
        }
      }

      & .u-text--white {
        color: foundation.$black;
        @include foundation.tb {
          color: foundation.$white;
          margin-left: 10px;
        }
        @include foundation.sp {
          margin-left: 10px;
        }
      }
      &_subtitleArea {
        margin-bottom: 20px;
        display: flex;
        justify-content: left;
        @include foundation.tb {
          margin-bottom: 30px;
        }
        @include foundation.sp {
          margin-bottom: 24px;
        }
        @include foundation.sp {
          justify-content: space-around;
        }

        & .u-text_size--M {
          @include foundation.tb {
            font-size: 1.5rem;
          }

          @include foundation.sp {
            font-size: 1.125rem !important;
          }
        }

        &_bg {
          @include foundation.tb {
            background-color: foundation.$p-blue;
            padding: 10px 20px;
            border-radius: 30px;
          }
          @include foundation.sp {
            padding: 4px 4px;
          }
        }

        & p {
          @include foundation.sp {
          }
          &:nth-child(2) {
            margin-left: 50px;
            @include foundation.sp {
              margin-left: 0;
            }
          }
        }

        & .u-text--white {
          @include foundation.sp {
            display: none;
          }
        }
      }

      &:nth-child(1) {
        position: relative;
        @include foundation.tb {
          width: calc(100% - 96%);
        }
        @include foundation.sp {
          width: 0;
        }

        & img {
          position: absolute;
          top: 0;
          right: 0%;
          z-index: -1;
          max-width: 692px;
          height: 600px;
          @include foundation.tb {
            max-width: inherit;
            width: 100vw;
            height: 400px;
          }
          @include foundation.sp {
            height: 500px;
            margin-top: 120px;
          }
        }
      }

      &_subtextArea {
        margin: 50px 0 60px !important;

        @include foundation.sp {
          margin: 160px 0 30px !important;
          text-align: center;
        }

        & .c-textArea_title {
          @include foundation.sp {
            text-align: left;
            font-size: 1.275rem;
          }
        }

        & .u-text_mark--yellow {
          display: inline-block;
          margin-top: 20px;

          @include foundation.sp {
            background-color: inherit;
            color: foundation.$yellow;
            margin-left: 0px;
            font-size: 1.625rem !important;
            padding: 20px 0;
          }
        }
      }

      & h2 {
        margin-bottom: 20px;
        @include foundation.tb {
          margin-bottom: 30px;
        }
        &.u-text--white {
          width: 100%;
          text-align: center;
          color: foundation.$white;
          margin-left: 0;

          @include foundation.sp {
            transform: scale(1, 1.2);
            padding: 14px 0;
          }
        }
      }
    }
  }

  & .u-text_size--M {
    @include foundation.tb {
      font-size: 1.5rem;
    }

    @include foundation.sp {
      font-size: 1.375rem;
    }
  }

  &.la-section {
    @include foundation.sp {
      padding-bottom: 30px !important;
      margin-bottom: 70px;
    }
  }
}

@media screen and (max-width: 380px) {
  .p-top_cont_cmn_subtextArea .u-text_mark--yellow {
    font-size: 1.5rem !important;
  }
}

@media screen and (max-width: 380px) {
  .p-top_cont_cmn:nth-child(1) img {
    height: 460px;
  }
}
@media screen and (max-width: 380px) {
  .p-top.la-section {
    margin-bottom: 20px !important;
  }
}

.p-worry {
  width: 100%;
  text-align: center;

  &_titleArea {
    display: inline-block;
    position: relative;
    &::before {
      content: " ";
      position: absolute;
      bottom: 20px;
      left: -40px;
      width: 0;
      height: 0;
      border-width: 50px 16px 0px 16px;
      border-style: solid;
      border-color: transparent;
      border-top-color: foundation.$blue;
      transform: rotate(-30deg);
      @include foundation.sp {
        left: -10px;
        bottom: 10px;
        border-width: 20px 5px 0px 5px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 20px;
      right: -40px;
      width: 0;
      height: 0;
      border-width: 50px 16px 0px 16px;
      border-style: solid;
      border-color: transparent;
      border-top-color: foundation.$blue;
      transform: rotate(30deg);
      @include foundation.sp {
        right: -10px;
        bottom: 10px;
        border-width: 20px 5px 0px 5px;
      }
    }
  }

  &_cont {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: foundation.$blue;
    padding: 50px 44px;
    border-radius: 14px;
    @include foundation.tb {
      padding: 30px 2px;
    }
    @include foundation.sp {
      padding: 14px;
      display: block;
    }

    &::before {
      content: " ";
      position: absolute;
      bottom: -40px;
      left: 50%;
      right: 50%;
      transform: translate(-50%, 0);
      width: 0;
      height: 0;
      border-width: 50px 36px 0px 36px;
      border-style: solid;
      border-color: transparent;
      border-top-color: foundation.$blue;
      @include foundation.sp {
        bottom: -20px;
        border-width: 30px 20px 0px 20px;
      }
    }

    &_cmn {
      background-color: foundation.$white;
      width: calc(100% - 70%);
      padding: 20px;
      margin: 10px;

      @include foundation.tb {
        min-height: 320px;
      }

      @include foundation.sp {
        min-height: inherit;
        margin: 10px 0;
        width: 100%;
      }

      & img {
        @include foundation.sp {
          width: 110px;
          height: 90px;
        }
      }

      &_inner {
        & p {
          text-align: justify;
        }
      }

      // & .u-text_mark--orange{
      //   color: foundation.$black;
      // }
    }
  }

  & .c-title--h3 {
    @include foundation.sp {
      font-size: 1.1875rem;
    }
  }
  & .u-text_size--L {
    @include foundation.sp {
      font-size: 1.75rem;
    }
  }
}

.p-solution {
  width: 100%;

  &_cont {
    width: 100%;
    &_inner {
      display: flex;
      justify-content: center;
      margin-top: 50px;
      @include foundation.tb {
        display: block;
        border-radius: 0;
      }
      &:nth-child(2) {
        flex-direction: row-reverse;
      }

      & img {
        @include foundation.tb {
          width: 100%;
        }
      }
      &_cmn {
        box-shadow: 5px 5px 10px foundation.$d-gray;
        border-radius: 11px;
        @include foundation.tb {
          box-shadow: 0px 5px 10px foundation.$d-gray;
          border-radius: 0;
        }

        &_textArea {
          padding: 30px 60px 0 60px;
          text-align: center;
          @include foundation.tb {
            padding: 40px 40px;
          }
          @include foundation.sp {
            text-align: center;
            padding: 20px 10px;
          }

          & p {
            text-align: justify;
          }
        }
      }
      // お客様に寄り添った
      &:nth-child(2) {
        & .p-solution_cont_inner_cmn {
          box-shadow: -5px 5px 10px foundation.$d-gray;
          @include foundation.tb {
            box-shadow: 0px 5px 10px foundation.$d-gray;
          }
        }
      }

      & img {
        max-width: 100%;
      }
    }
  }
  & br {
    @include foundation.tb {
      display: block;
    }
  }

  & .u-text_size--L {
    @include foundation.sp {
      font-size: 1.75rem;
    }
  }

  & .u-text_size--L {
    @include foundation.fz(32.5, 17.5);
      @include foundation.tb{
        font-size: 2.75rem
      }
      @include foundation.sp {
        font-size: 1.875rem;
      }
  }
}

@media screen and (max-width: 380px) {
  .p-solution h3 .u-text_size--L 
  {
    font-size: 1.875rem !important;
  }
}

