.p-merit {
  width: 100%;
  background-color: foundation.$gray;
  text-align: center;
  &_cont {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include foundation.sp {
      display: block;
    }

    &_inner {
      width: calc(100% - 69%);
      @include foundation.tb {
        width: calc(100% - 51%);
      }
      @include foundation.sp {
        width: 100%;
      }

      & img {
        width: 110px;
        height: 98px;
      }

      &_cmn {
        width: 100%;
        padding: 30px 36px 0;
        min-height: 400px;
        background-color: foundation.$white;
        border-radius: 8px;
        @include foundation.tb {
          padding: 20px 26px 0;
          min-height: 410px;
        }
        @include foundation.sp {
          padding: 18px 10px 0;
          min-height: 330px;
        }

        & p {
          text-align: justify;
        }
      }
    }
  }

  & .c-titleArea {
    & br {
      display: none;
      @include foundation.tb {
        display: block;
      }
    }
  }

  & .u-text_size--L {
    @include foundation.sp {
      font-size: 1.75rem;
    }
  }
}



.p-message {
  text-align: center;
  width: 100%;
  &_cont {
    display: flex;
    justify-content: space-around;
    
    @include foundation.tb{
      justify-content: space-between;
    }

    @include foundation.sp {
      display: block;
    }

    &_cmn {
      &:nth-child(1) {
        & img{
          @include foundation.tb{
            width: 280px;
            height: 330px;
          }
        }
      }
      &:nth-child(2) {
        
        width: calc(100% - 40%);

        @include foundation.tb {
          width: calc(100% - 45%);
        }
        @include foundation.sp {
          margin-top: 40px;
          width: 100%;
        }
      }

      &_textArea {
        & p {
          text-align: justify;

          &:nth-child(2) {
            text-align: right;
          }
        }
      }
    }
  }
}

.p-message_cont_cmn2{
  max-width: 720px;
}

.p-example {
  width: 100%;
  text-align: center;

  &_cont {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include foundation.sp {
      margin-top: 60px;
      display: block;
    }

    &_cmn {
      width: calc(100% - 52%);
      @include foundation.sp {
        width: 100%;
      }

      &:nth-child(1) {
        margin-bottom: 30px;
      }

      & img {
        max-width: 500px;
        width: 100%;
      }
    }
  }

  &_textArea {
    display: inline-block;
    padding: 35px 18px;
    border: 3px solid foundation.$blue;
    border-radius: 13px;
    @include foundation.sp {
      text-align: justify;
      & br {
        display: none;
      }
    }
  }
}

.p-flow {
  width: 100%;
  background-color: foundation.$bg-blue;

  &_cont {
    width: 80%;
    margin: 0 auto;
    @include foundation.tb{
      width: 100%;
    }

    &_inner {
      width: 100%;
      position: relative;
      border: 1px solid foundation.$black;
      background-color: foundation.$white;
      border-radius: 6px;

      &::after {
        content: "";
        position: absolute;
        bottom: -60px;
        right: 50%;
        transform: translate(50%, 0);
        width: 0;
        height: 0;
        border-width: 40px 40px 0px 40px;
        border-style: solid;
        border-color: transparent;
        border-top-color: foundation.$dp-gray;
        @include foundation.sp {
          bottom: -40px;
          border-width: 20px 20px 0px 20px;
        }
      }

      &:last-child {
        &::after {
          display: none;
        }
      }

      &_cmn {
        position: absolute;
        background-color: foundation.$blue;
        min-width: 130px;
        height: 100%;
        display: flex;
        align-items: center;
        border-radius: 4px 0 0 4px;
        @include foundation.sp {
          min-width: 40px;
        }

        & .c-textArea_title {
          @include foundation.sp {
            font-size: 1rem;
          }
        }

        &::after {
          content: " ";
          position: absolute;
          top: 0px;
          bottom: 0%;
          right: -39.6px;
          width: 0;
          height: 0;
          border-width: 39px 0px 40px 40px;
          border-style: solid;
          border-color: transparent;
          border-left-color: foundation.$blue;

          @include foundation.sp {
            border-width: 33.8px 0px 33.8px 19.5px;
            right: -19.6px;
          }
        }
      }

      &_label {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        background-color: foundation.$orange;
        color: foundation.$white;
        top: 0;
        width: 200px;
        height: 100%;
        right: 0;
        border-radius: 0px 4px 4px 0px;
        @include foundation.tb {
          width: 170px;
        }
        @include foundation.sp {
          width: 50px;
        }
      }

      &_text {
        padding: 20px 0;
        text-align: center;
        @include foundation.sp {
          font-size: 1.0625rem !important;
        }
      }
    }
  }
}
@media screen and (max-width: 380px) {
  .p-flow_cont_inner_cmn::after {
    right: -19px;
  }
}

.p-plan {
  width: 100%;
  text-align: center;

  &_cont {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &_inner {
      position: relative;
      width: calc(100% - 68%);
      border: 1px solid foundation.$black;
      border-radius: 8px;
      max-width: 412.719px;
      @include foundation.tb {
        width: calc(100%);
        max-width: 900px;
        margin-bottom: 60px;
      }

      &_subtitleArea {
        position: absolute;
        display: flex;
        justify-content: space-around;
        max-width: 390.719px;
        right: 10px;
        left: 10px;
        top: -20px;
        background-color: foundation.$white;
        border-radius: 8px;
        border: 1px solid foundation.$black;
        border-radius: 28px;
        @include foundation.tb {
          max-width: 900px;
        }

        & span {
          background-color: foundation.$black;
          color: white;
          padding: 4px 12px;
          border-radius: 28px;
        }

        & p {
          text-align: center;
          padding: 4px 20px 4px 0px;
          font-size:18px ;
          @include foundation.sp{
            font-size:16px;
          }
        }
      }

      &_titleArea {
        padding: 30px 0 20px;
        border-radius: 8px 8px 0 0;
        background-color: foundation.$pl-basic-blue;

        & p {
          text-align: center;
        }
      }

      &_cmn {
        width: 100%;

        & tr {
          width: 100%;
          border-bottom: 1px solid foundation.$black;
          &:last-child {
            border-bottom: 0px;

            & th {
              border-radius: 0px 0px 0px 8px;
            }
          }
        }

        & th {
          position: relative;
          max-width: 143.75px;
          width: calc(100% - 65%);
          background-color: foundation.$pl-basic-cont-blue;
          height: 80px;

          & p {
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }

        & td {
          position: relative;
          width: calc(100% - 34%);

          & p {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            padding: 0 10px;
          }
        }
      }

      &:nth-child(2) {
        & .p-plan_cont_inner_titleArea {
          background-color: foundation.$blue;
        }

        & th {
          background-color: foundation.$pl-stand-cont-blue;
        }
      }
      &:nth-child(3) {
        & .p-plan_cont_inner_titleArea {
          background-color: foundation.$orange;
        }

        & th {
          background-color: foundation.$pl-premium-cont-orange;
        }
      }
    }
  }
}

@media screen and (max-width: 380px) {
  .p-plan_cont_inner_titleArea .u-text_size--L {
    font-size: 2.75rem !important;
  }
}

