@use "../../foundation";
.u-text {

  &_size {
    &--LL{
        @include foundation.fz(50);
      @include foundation.tb{
        font-size: 2.75rem
      }
      @include foundation.sp{
        font-size: 2.25rem
      }
    }
    &--L {
      @include foundation.fz(31.25, 18.75);
      @include foundation.tb{
        font-size: 2.75rem
      }
    }
    &--M {
      @include foundation.fz(23.75, 15);
      @include foundation.tb{
        font-size: 1.75rem
      }
      
    }

    &--S{
        @include foundation.fz(10,8.75)
    }
    &--SS{
        @include foundation.fz(7.5,6.25)
    }
  }

  &_lh {
    &--L {
      @include foundation.lh(2);
    }
  }

  &_mark {
    &--blue {
      background-color: foundation.$p-blue;
    }

    &--orange{
        background-color: foundation.$orange;
    color: foundation.$white;
    }

    &--yellow{
        background-color: foundation.$yellow;
    }

    &--red{
        background-color: foundation.$red;
        color:foundation.$white;
        padding:2px;
    }
  }

  &_weight {
    &--bold {
      font-weight: bold;
    }
  }

  &--orange {
    color: foundation.$orange;
  }
  &--white {
    color: foundation.$white;
  }

  &_line {
    &--blue {
      border-bottom: 8px solid foundation.$blue;
    }
    &--orange {
      &::after {
        content: "";
        position: absolute;
        bottom: -26px;
        right: 50%;
        left: 50%;
        transform: translate(-50%, 0);
        width: 80px;
        height: 8px;
        border-radius: 8px;
        background-color: foundation.$orange;
        @include foundation.tb{
            bottom:-18px;
        }
      }
    }

    &--orangeBottom{
        border-bottom: 8px solid foundation.$orange;
    }
    &--dpgray{
        border-bottom: 1px solid foundation.$dp-gray;
    }
  }

  &_ls{
    &--M{
        letter-spacing: 3px;
        @include foundation.sp{
            letter-spacing: 1px;
        }
    }
  }
}


@media screen and (max-width: 380px){
    .u-text_size--L {
        font-size: 1.5rem !important;
    }
}
@media screen and (max-width: 380px){
    .u-text_size--LL {
        font-size: 1.875rem;
    }
}