@use '../foundation';
.la-container {
    width: 83%;
    max-width: 1328px;
    margin: 0 auto;
    &--big {
        width:90%;
        @extend .la-container;
        max-width: 1434px;
        @include foundation.sp{
            width: 95%;
        }
    }
    &--min {
        width:71%;
        @extend .la-container;
        max-width: 1000px;

        @include foundation.sp{
            width:90%;
        }
    }
}