@use '../../foundation';
.c-textArea{
  margin-top:30px;
  &_title{
      @include foundation.fz(15,13.75);
      @include foundation.lh(1.6);
  }
  &_text{
      width:100%;
      @include foundation.fz(12.5,11.25);
      @include foundation.tb{
        font-size:1.125rem;
      }
      @include foundation.lh(1.7);
      text-align: left;
  }

}
