@mixin pc($width: 1025px) {
	@media screen and (min-width: $width) {
		@content;
	}
}

@mixin tb($width: 1024px) {
	@media screen and (max-width: $width) {
		@content;
		// contentををつけることによって別の内容のCSSを書くことができる
	}
}

@mixin sp($width: 450px) {
	@media screen and (max-width: $width) {
		@content;
		// contentををつけることによって別の内容のCSSを書くことができる
	}
}

// font-size
@mixin fz($font_pc: "", $font_sp: ""){
  font-size: #{$font_pc}px;
  font-size: #{$font_pc / 10}rem;

  @if ($font_sp != "") {
    @include sp {
      font-size: #{$font_sp}px;
      font-size: #{$font_sp / 10}rem;
    }
  }
}
// line-height
@mixin lh($lineH_pc: "", $lineH_sp: ""){
	line-height: #{$lineH_pc}em;
	@if ($lineH_sp != "") {
		@include sp {
			line-height: #{$lineH_sp}em;
		}
	}
}

@mixin animation(
	$name,
	$duration:1s,
	$timing-function:ease,
	$delay:0s,
	$iteration-cont:1, 
	$direction:normal,
	$fill-mode:forwards
	)
{
	animation: {
		name:$name;
		duration:$duration;
		timing-function:$timing-function;
		delay:$delay;
		iteration-count:$iteration-cont;
		direction:$direction;
		fill-mode:$fill-mode;
	}
}