@use '../foundation';


.l-header {
  z-index: 100000000;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 120px;
  width: 100vw;
  height: 100px;
  background-color: foundation.$white;
  
  @include foundation.sp {
    padding: 0 60px;
    height: 50px;
    
  }
  &_cont {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 50px;
    @include foundation.tb {
      top: 0;
      left: 0;
      transform: translate(0, 0);
      padding: 5px 10px;
    }

    &_title {
        width: 150px;
        height: 100px;
      letter-spacing: 0.03em;
      color: foundation.$blue;

      @include foundation.sp{
        height: 75px;
      }

      & img {
        @include foundation.tb {
          width: 150px;
          height: 90px;
        }
        @include foundation.sp {
          width: 150px;
          height: 48px;
        }
      }
    }
    &_text {
      letter-spacing: 0.03em;
      margin-top: 10px;
    }
  }
  &_menu {
    position: relative;
    @include foundation.tb {
      display: none;
    }

    &_inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @include foundation.tb {
        flex-direction: column;
        justify-content: space-around;
        margin-top:90px;
      }

      &_cmn {
        position: relative;
        cursor: pointer;
        letter-spacing: 0.03em;
        margin-left: 20px;
        @include foundation.tb {
          width:100%;
          margin: 20px 0;
          padding: 0;
        }
        
        & a{

            &:hover{
                color:foundation.$blue;
                @include foundation.tb{
                    color: foundation.$white;
                }
            }
        }

        &:nth-child(2) {
          position: relative;
          cursor: pointer;

          @include foundation.tb{
            margin:0;
            padding:20px 0;
          }

          & span{
            &::after {
              content: "";
              position: absolute;
              top: 47px;
              right: -10px;
              z-index: 100;
              
  
              border-right: 4px solid transparent;
              border-top: 7px solid foundation.$white;
              border-left: 4px solid transparent;
              transition:0.2s ease;

              @include foundation.tb{
                border-top: 7px solid foundation.$white;
                top:26px;
                right:34%;
              }
            }
          }

        }
        &:nth-child(3) {
          position: relative;
          cursor: pointer;

          @include foundation.tb{
            margin: 0;
            padding:20px 0;
          }

          & span{
            &::after {
              content: "";
              position: absolute;
              top: 47px;
              right: -10px;
              z-index: 100;
  
              border-right: 4px solid transparent;
              border-top: 7px solid foundation.$white;
              border-left: 4px solid transparent;
              transition:0.2s ease;

              @include foundation.tb{
                border-top: 7px solid foundation.$white;
                top:26px;
                right:20%;
              }
            }
          }

        }

        &:last-child {
          & img{
            width: 30px;
            height: 30px;
            display: inline-block;
          }
        }

        & ul {
          letter-spacing: 0.03em;
          position: absolute;
          transition: 0.5s;
          width: 200px;
          top: 99px;
          color: foundation.$white;
          opacity: 0;
          visibility: hidden;
          @include foundation.tb{
            position:absolute;
            display: none;
            top:20px;
            margin: 0 auto;
            width: 100%;
            background-color: inherit;
            opacity: 1;
            visibility: visible;
          }

          & li{
            & a {
              text-align: center;
              display: block;
              width: 100%;
              line-height: 26px;
              padding: 20px 0;
              border-bottom: 1px solid foundation.$white;
              @include foundation.tb{
                padding: 12px 0;
                border-bottom: 0px;
              }
            }
          }
        }
      }
    }
  }
  &_mask {
    display: none;
    transition: all 0.5;
  }
}

.l-header_toggle {
  @include foundation.pc {
    visibility: hidden;
  }
  @include foundation.tb {
    visibility: visible;
  }
  @include foundation.sp {
    visibility: visible;
  }

  display: inline-block;
  position: absolute;
  z-index: 1000000000;
  width: 40px;
  height: 30px;
  color: foundation.$blue;

  @include foundation.tb {
    cursor: pointer;
    top: 50%;
    right: 2%;
    transform: translate(-50%, -50%);
  }

  &_bar {
    position: absolute;
    height: 3px;
    width: 40px;
    border-radius: 16px;
    background-color: foundation.$blue;
    transition: ease 0.3s;

    @include foundation.tb {
      height: 2.5px;
      width: 26px;
      border-radius: 10px;
    }

    &:nth-of-type(1) {
      top: 4px;
    }
    &:nth-of-type(2) {
      top: 14px;
    }
    &:nth-of-type(3) {
      top: 24px;
    }
  }
}

.l-header.open {
  .l-header_menu {
    display: block;
    width: calc(100% - 50%);
    height:100vh;
    position: fixed;
    top: 0;
    right: 0;
    margin: auto;
    text-align: center;
    align-items: center;
    height: 100vh;
    background-color: foundation.$blue;
    color: white;
    z-index: 10000000;
  }

  .l-header_toggle_bar {
    background-color: foundation.$white;
    &:nth-of-type(1) {
      -webkit-transform: translateY(10px) rotate(-315deg);
      transform: translateY(10px) rotate(-315deg);
    }
    &:nth-of-type(2) {
      opacity: 0;
    }
    &:nth-of-type(3) {
      -webkit-transform: translateY(-10px) rotate(315deg);
      transform: translateY(-10px) rotate(315deg);
    }
  }

  &body{
    @include foundation.tb{

      width: 100%;
      height: 100%;
      position:fixed;
    }
  }
}


// span付与の部分
.l-header_menu_inner_cmn_down_list{
  & span{
    display: none;
  }

    & .l-header_menu_inner_cmn ul{
      display: block !important;
      & li {
        @include foundation.animation($name: kf-dropdown, $duration: 0.5s);
      }
    }
  
}

