.down_list{
  padding:40px 14px 40px 0;

  @include foundation.tb{
    padding:0;
  }
  &:hover{
    @include foundation.pc{

      position: relative;
      & .sub-menu{
        cursor: pointer;
        position: absolute;
        opacity: 1;
        visibility: visible;
        @include foundation.tb{
          display: block;
          position: relative;
          width: 100%;
        }
        & li{
          @include foundation.animation(
            $name: kf-dropdown, 
            $duration: 0.2s
            );
        }
        
      }
    }
  }
}

.sub-menu.inview{
  @include foundation.tb{
    opacity: 1;
    position: relative;
    visibility: visible;
    display: block;
  
    @include foundation.animation(
      $name:kf-dropdown,
      $duration:0.5s
    )
  }
}

@keyframes kf-dropdown {
  0% {
    display: block;
    transform: translateY(-10%);

  }

  100% {
    display: block;
    transform: translateY(0%);
   
  }
}

