@use '../../foundation';
.c-button {
  position: relative;
  display: inline-block;
  color: foundation.$white;
  background-color: foundation.$orange;
  padding: 20px 90px;
  border-radius: 40px;
  box-shadow: 0px 5px 10px foundation.$p-black;
  transition: ease-in-out;

  // @include foundation.animation(
  //   $name:kf-button,
  //   $duration:1.2s
  // );

  &:hover{
    @include foundation.animation(
    $name:kf-button,
    $duration:0.5s
  );
  }
}

@keyframes kf-button {
  0%{
    background-color: foundation.$orange;
    // box-shadow: 0px 5px 10px foundation.$p-black;
  }

  100%{
    background-color: foundation.$p-orange;
    // box-shadow: 0px 0px 0px foundation.$p-black;
  }
}
